// Sidenav navbar

.sidenav.navbar {
  background-color: var(--#{$prefix}card-background);
  border-radius: 0.5rem;
  @media (max-width: 576px) {
    border-radius: 0rem 0rem 0.5rem 0.5rem;
    border-top: 1px solid var(--#{$prefix}gray-200);
    padding: 1rem 1.5rem;
  }
  @media (min-width: 576px) and (max-width: 767.98px) {
    border-radius: 0rem 0rem 0.5rem 0.5rem;
    border-top: 1px solid var(--#{$prefix}gray-200);
    padding: 1rem 1.5rem;
  }
  .navbar-header {
    color: var(--#{$prefix}gray-500);
    padding: 0.3rem 0.4rem;
    margin-bottom: 0.2rem;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-size: 0.6875rem;
    font-weight: 500;
  }
  .navbar-nav {
    width: 100%;
    .nav-item {
      margin-bottom: 0.125rem;
      &.active {
        background-color: var(--#{$prefix}gray-200);
        border-radius: 0.5rem;
        color: var(--#{$prefix}gray-500) !important;
      }
      .nav-link {
        padding: 0.3rem 0.7rem 0.3rem 0.7rem !important;
        font-weight: 500;
        background-color: transparent;
        border-radius: 0.5rem;
        color: var(--#{$prefix}gray-600) !important;
        position: relative;
        display: flex;
        align-items: center;
        border-bottom: 0px;
        .nav-icon {
          opacity: 0.5;
          margin-right: 0.5rem;
        }
        &:hover {
          background-color: var(--#{$prefix}gray-200) !important;
          color: var(--#{$prefix}gray-500) !important;
          .nav-icon {
            opacity: 1;
          }
        }
      }
    }
    .active > .nav-link {
      color: var(--#{$prefix}gray-500) !important;
      .nav-icon {
        opacity: 1;
      }
    }
  }
}
